import React, {useEffect} from 'react'
import $ from 'jquery'
import slick from 'slick-carousel'
import HeroImage1 from '../images/hero-images/hero-image-1.jpg'
import HeroImage2 from '../images/hero-images/hero-image-2.jpg'
import HeroImage3 from '../images/hero-images/hero-image-3.jpg'
import HeroImage4 from '../images/hero-images/hero-image-4.jpg'

const slides = [
    {
        image: HeroImage1,
        mainText: 'Expedite Your<br /> <span>Outcomes.</span>',
        subText: 'We can deliver the pertinent digital platforms'
    },
    {
        image: HeroImage2,
        mainText: 'Augment Your <br />  <span>Growth.</span>',
        subText: 'We can help you succeed on the internet',
        action: {
            'link': '/'
        }
    },
    {
        image: HeroImage3,
        mainText: 'Focus On <br /><span> Your Business.</span>',
        subText: 'Let us manage your digital assets for exceptional results',
        action: {
            'link': '/'
        }
    },
    {
        image: HeroImage4,
        mainText: 'Simplify<br /> <span>Your IT.</span>',
        subText: 'We can rationalize your digital footprint for optimum returns.',
        action: {
            'link': '/'
        }
    }
]

const HeroSlides = () => {

    useEffect(() => {
        const fcshinit = $('.fullscreen-slider')
        const fssc = $('.fullscreenslider-counter')
        const sbp = $(".fsc-slider-prev")
        const sbn = $(".fsc-slider-next")
        const ccsi = $(".cur_carousel-slider-container")
        const scw = $(".slider-carousel-wrap")

        sbp.on("click", function () {
            $(this).closest(scw).find(ccsi).slick('slickPrev')
        })
        sbn.on("click", function () {
            $(this).closest(scw).find(ccsi).slick('slickNext')
        })

        fcshinit.on("init", function (event, slick) {
            fssc.text(Number(slick.currentSlide + 1) + ' / ' + slick.slideCount)
        })
        fcshinit.slick({
            infinite: true,
            slidesToShow: 1,
            dots: true,
            arrows: false,
            adaptiveHeight: false
        })
        fcshinit.on("afterChange", function (event, slick, currentSlide) {
            fssc.text(Number(slick.currentSlide + 1) + ' / ' + slick.slideCount)
        })
    }, [])

    return (
        <div className="hero-wrap" id="sec1" data-scrollax-parent="true">
            <div className="slider-carousel-wrap full-height fullscreen-slider-wrap">
                <div className="fullscreen-slider full-height cur_carousel-slider-container fl-wrap" data-slick='{"autoplay": true, "autoplaySpeed": 4000 , "pauseOnHover": false}'>
                    <div className="fullscreen-slider-item full-height fl-wrap">
                        <div className="bg par-elem"  data-bg="../images/hero-images/hero-image-3.jpg"></div>
                        <div className="overlay"></div>
                        <div className="half-hero-wrap">
                            <h1>Hey there!<br />I'm  Martin Solonick<br />Independent <span> Digital  Designer </span></h1>
                            <h4>I create web and graphic design</h4>
                            <div className="clearfix"></div>
                            <a href="#sec2" className="custom-scroll-link btn float-btn flat-btn color-btn mar-top">Let's Start</a>
                        </div>
                    </div>
                    <div className="fullscreen-slider-item full-height fl-wrap">
                        <div className="bg par-elem"  data-bg="../images/hero-images/hero-image-1.jpg"></div>
                        <div className="overlay"></div>
                        <div className="half-hero-wrap">
                            <h1>Design<br /> classNamey and Stylish <br />  <span>Brand Perception.</span></h1>
                            <h4>I create web and graphic design</h4>
                            <div className="clearfix"></div>
                            <a href="portfolio.html" className="btn float-btn flat-btn color-btn mar-top">My Portfolio</a>
                        </div>
                    </div>
                    <div className="fullscreen-slider-item full-height fl-wrap">
                        <div className="half-hero-wrap">
                            <h1>Original Design <br /> Features <br />With High  <span> Quality  Code.</span></h1>
                            <h4>I create web and graphic design</h4>
                            <div className="clearfix"></div>
                            <a href="services.html" className="btn float-btn flat-btn color-btn mar-top">My services</a>
                        </div>
                        <div className="bg par-elem"  data-bg="../images/hero-images/hero-image-2.jpg"></div>
                        <div className="overlay"></div>
                    </div>
                </div>
                <div className="sp-cont   sp-cont-prev fsc-slider-prev"><i className="fal fa-arrow-left"></i></div>
                <div className="sp-cont   sp-cont-next fsc-slider-next"><i className="fal fa-arrow-right"></i></div>
                <div className="fullscreenslider-counter"></div>
            </div>
            <div className="hero-decor-numb"><span>40.7143528  </span><span>-74.0059731 </span> <a href="https://www.google.com.ua/maps/" target="_blank" className="hero-decor-numb-tooltip">Based In NewYork</a></div>
        </div>
    )
}

export default HeroSlides
